.container__work-title
{
  padding-top: 6rem;
  padding-bottom: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container__work-sub
{
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 1rem;
  padding-top: 1rem; /* decrease the padding-top value */
}


.work {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  height: 100hv;
}

.card-container {
  margin: 1rem;
  padding-top: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  
  border-radius: 10%;
  
}

.flip-card {
  background-color: transparent;
  border: solid 0.5em transparent;
 
  background-clip: content-box, border-box;
  width: 60vmin;
  height: 50vmin;
  perspective: 1000px;
  font-family: sans-serif;
  margin: 0 8px 16px 8px; /* add bottom margin to create space between rows */
  

}

  
.title {
  font-size: small;
  text-align: center;
}

  
  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
  }
  
  .flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
    
  }
  
  .flip-card-front, .flip-card-back {
    box-shadow: 0 8px 14px 0 rgba(0,0,0,0.2);
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border: 1px solid var;
    border-radius: 1rem;
  }
  
  .flip-card-front {
  background: linear-gradient(120deg, #e8e8e8 0%, #fff 90%,
     rgb(255, 211, 195) 40%, var(--color-crimson) 48%);
  color: 	#800080;
}
  
  .flip-card-back {
    background: linear-gradient(120deg, #f6f6f5 30%, white 88%,   40%, var(--color-crimson) 78%);
    color: grey;
    transform: rotateY(180deg);
    flex-shrink: 0;
    margin: 0;
    padding: -10rem;
    margin-top: -10px; /* add negative margin-top to bring the text up */
  }
  

 


  .container__links
  {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
