.background-img {
/*  background-image: url("../../../Assets//hobbiesHead1.jpg");*/
  background: "../../../Assets/back.svg";
  height: 400px;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 5% auto;
  padding: 20px;
  border: 1px solid #2a3cad;
  border-radius: 4px;
  box-shadow: 0px 0px 5px #2a3cad;
  position: relative;
}

@media only screen and (min-width: 768px) {
  .background-img {
    width: 100vw;
    max-width: none;
    margin: 0;
    height: 600px;
  }

  .head-content h2
  {
    padding: 1rem;
  }
}


@media only screen and (max-width: 767px) {
  .background-img {
    width: 100%;
    margin-top: 8rem;
    border: none;
    border-radius: 0;
    box-shadow: none;
  }

  .head-content p
  {
    display: none;
  }
}

.box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 80%;
  background: #111845a6;
  box-sizing: border-box;
  overflow: hidden;
  box-shadow: 0 20px 50px rgb(23, 32, 90);
  border: 2px solid #2a3cad;
  color: white;
  padding: 20px;
}

@media only screen and (max-width: 767px) {
  .box {
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    transform: none;
  }

  
  .head-content h2
  {
    display: none;
  }

  .head-content p
  {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .box {
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      transform: none;
  }
  .head-content h2
  {
    padding: 1rem;
  }

  .head-content p
  {
    padding: 1rem;
  }
}

.content {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;
}


.box:before{
  content: '';
  position:absolute;
  top:0;
  left:-100%;
  width:100%;
  height:100%;
  background: rgba(255,255,255,0.3);
  transition:0.5s;
  pointer-events: none;
}

.box:hover:before{
  left:-50%;
  transform: skewX(-5deg);
}

.box .content{
  position:absolute;
  top:15px;
  left:15px;
  right:15px;
  bottom:15px;
  border:1px solid #f0a591;
  padding:20px;
  text-align:center;
  box-shadow: 0 5px 10px rgba(9,0,0,0.5);
  
}

.box span{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
  box-sizing: border-box;
  
}

.box span:nth-child(1)
{
  transform:rotate(0deg);
}

/*
.box span:nth-child(2)
{
  transform:rotate(90deg);
}*/

.box span:nth-child(3)
{
  transform:rotate(180deg);
}

/*
.box span:nth-child(4)
{
  transform:rotate(270deg);
}*/

.box span:before
{
  content: '';
  position: absolute;
  width:100%;
  height: 2px;
  background: var(--color-golden);
  animation: animate 4s linear infinite;
}

    @keyframes animate {
      0% {
      transform:scaleX(0);
      transform-origin: left;
      }
      50%
      {
        transform:scaleX(1);
      transform-origin: left;
      }
      50.1%
      {
        transform:scaleX(1);
      transform-origin: right;
        
      }
      
      100%
      {
        transform:scaleX(0);
      transform-origin: right;
        
      }
      
      
    } 
    

    .head-content
    {
      padding: 1rem;
    }

    .head-content h2
    {
      padding: 1rem;
    }

    .head-content p
    {
      padding: 1rem;
    }